<template>
	<view class="container">
		<view class="main">
			<view class="search-bar">
				<view class="search-input-wrap">
					<view class="search-icon">
						<text class="iconfont">üîç</text>
					</view>
					<input class="search-input" type="text" v-model="searchKey" placeholder="ÊêúÁ¥¢ÂÆ¢Êà∑ÂßìÂêç" @confirm="searchCustomers" />
				</view>
				<view class="search-btn" @click="searchCustomers">ÊêúÁ¥¢</view>
			</view>

			<view class="customer-list">
				<view class="customer-card" v-for="(item, index) in pendingCustomerList" :key="index">
					<view class="customer-info">
						<view class="customer-header">
							<text class="customer-name">{{ item.name }}</text>
							<text class="customer-status" :class="'status-' + getStatusClass(item.deal_status)">{{ getDealStatus(item.deal_status) }}</text>
						</view>
						<view class="info-row">
							<text class="info-label">ÊâãÊú∫Âè∑:</text>
							<text class="info-value">{{ item.phone }}</text>
						</view>
						<view class="info-row">
							<text class="info-label">ÊâÄÂ±ûÂÆ¢Áæ§:</text>
							<text class="info-value">{{ getClientType(item.client_type) }}</text>
						</view>
						<view class="info-row">
							<text class="info-label">‰∏öÂä°Âëò:</text>
							<text class="info-value">{{ item.service_name }}</text>
						</view>
						<view class="info-row">
							<text class="info-label">‰∫ßÂìÅÂëò:</text>
							<text class="info-value">{{ item.product_name || 'Êú™ÂàÜÈÖç' }}</text>
						</view>
					</view>
					<view class="customer-actions">
						<view class="action-btn view-btn" @click="handleViewCustomer(item)">
							<text class="btn-text">Êü•ÁúãÂÆ¢Êà∑</text>
						</view>
						<view class="action-btn loan-btn" @click="handleCreateLoan(item)">
							<text class="btn-text">Áî≥ËØ∑Ë¥∑Ê¨æ</text>
						</view>
					</view>
				</view>

				<view class="empty-tip" v-if="pendingCustomerList.length === 0 && !isLoading">
					ÊöÇÊó†ÂæÖÂ§ÑÁêÜÂÆ¢Êà∑Êï∞ÊçÆ
				</view>
				
				<!-- Âä†ËΩΩÊõ¥Â§öÊèêÁ§∫ -->
				<view class="loading-more" v-if="pendingCustomerList.length > 0">
					<text v-if="isLoading">Âä†ËΩΩ‰∏≠...</text>
					<text v-else-if="hasMore">‰∏äÊãâÂä†ËΩΩÊõ¥Â§ö</text>
					<text v-else>Ê≤°ÊúâÊõ¥Â§öÊï∞ÊçÆ‰∫Ü</text>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
import financeApi from '@/api/finance.js';
import tabbarUtils from '../../utils/tabbarUtils.js';

export default {
	data() {
		return {
			activeTab: 0,
			searchKey: '',
			customerForm: {
				name: '',
				phone: '',
				idCard: '',
				age: '',
				marriage: null
			},
			loanForm: {
				product: null,
				amount: '',
				term: null,
				purpose: null,
				monthlyIncome: ''
			},
			uploadList: [
				{
					label: 'Ë∫´‰ªΩËØÅÊ≠£Èù¢',
					path: ''
				},
				{
					label: 'Ë∫´‰ªΩËØÅÂèçÈù¢',
					path: ''
				},
				{
					label: 'Â∑•‰ΩúËØÅÊòé',
					path: ''
				},
				{
					label: 'Êî∂ÂÖ•ËØÅÊòé',
					path: ''
				}
			],
			pendingCustomerList: [],
			// ÂàÜÈ°µÁõ∏ÂÖ≥Áä∂ÊÄÅ
			currentPage: 1,
			pageSize: 10,
			total: 0,
			isLoading: false,
			hasMore: true
		}
	},
	onLoad(option) {
		
		// Â¶ÇÊûú‰ªéÂÆ¢Êà∑È°µÈù¢Ë∑≥ËΩ¨ËøáÊù•Ôºå‰ºöÂ∏¶‰∏äÂÆ¢Êà∑ÂêçÁß∞
		if (option.customer) {
			this.customerForm.name = option.customer;
		}

		// Âä†ËΩΩÂæÖÂ§ÑÁêÜÂÆ¢Êà∑ÂàóË°®
		this.loadPendingCustomers(true);
	},
	onShow() {
		// App.vue ‰∏≠Â∑≤ÁªèÂ§ÑÁêÜ‰∫Ü tabbar Áõ∏ÂÖ≥ÈÄªËæëÔºåËøôÈáå‰∏çÂÜçÈúÄË¶ÅÂçïÁã¨Â§ÑÁêÜ
		// Ê£ÄÊü•ÊùÉÈôê
		const permissions = uni.getStorageSync('permissions');
		if (!permissions || !permissions.loan) {
			uni.showToast({
				title: 'ÊÇ®Ê≤°ÊúâÊùÉÈôêËÆøÈóÆË¥∑Ê¨æÁÆ°ÁêÜ',
				icon: 'none',
				duration: 2000
			});
			
			// Âª∂ËøüÂêéË∑≥ËΩ¨Âà∞È¶ñÈ°µ
			setTimeout(() => {
				uni.switchTab({
					url: '/pages/dashboard/dashboard'
				});
			}, 1000);
			return;
		}
	},
	// ‰∏äÊãâËß¶Â∫ï‰∫ã‰ª∂
	onReachBottom() {
		if (this.hasMore && !this.isLoading) {
			this.loadMore();
		}
	},
	methods: {
		// ÂàáÊç¢Ê†áÁ≠æ
		switchTab(index) {
			this.activeTab = index;
			if (index === 1) {
				this.loadPendingCustomers(true);
			}
		},

		// Âä†ËΩΩÊõ¥Â§öÊï∞ÊçÆ
		loadMore() {
			if (this.hasMore && !this.isLoading) {
				this.currentPage++;
				this.loadPendingCustomers(false);
			}
		},

		// Âä†ËΩΩÂæÖÂ§ÑÁêÜÂÆ¢Êà∑ÂàóË°®
		async loadPendingCustomers(isReset = true) {
			// Â¶ÇÊûúÊòØÈáçÁΩÆÔºåÂàôÊ∏ÖÁ©∫ÂàóË°®Âπ∂ÂõûÂà∞Á¨¨‰∏ÄÈ°µ
			if (isReset) {
				this.currentPage = 1;
				this.pendingCustomerList = [];
				this.hasMore = true;
			}
			
			// Â∑≤Êó†Êõ¥Â§öÊï∞ÊçÆÊó∂‰∏çÂÜçËØ∑Ê±Ç
			if (!this.hasMore) {
				return;
			}
			
			this.isLoading = true;
			
			if (isReset) {
				uni.showLoading({
					title: 'Âä†ËΩΩ‰∏≠...'
				});
			}
			
			try {
				const response = await financeApi.getFinanceList({
					name: this.searchKey,
					page: this.currentPage,
					page_size: this.pageSize
				});
				
				if (response && response.retCode === 200) {
					const newList = response.data.list || [];
					
					// ËøΩÂä†Êï∞ÊçÆËÄåÈùûÊõøÊç¢
					if (isReset) {
						this.pendingCustomerList = newList;
					} else {
						this.pendingCustomerList = [...this.pendingCustomerList, ...newList];
					}
					
					this.total = response.data.total || 0;
					
					// Âà§Êñ≠ÊòØÂê¶ËøòÊúâÊõ¥Â§öÊï∞ÊçÆ
					this.hasMore = newList.length >= this.pageSize && this.pendingCustomerList.length < this.total;
					
					if (isReset && this.pendingCustomerList.length === 0) {
						uni.showToast({
							title: 'ÊöÇÊó†Ë¥∑Ê¨æÊï∞ÊçÆ',
							icon: 'none'
						});
					}
				} else {
					uni.showToast({
						title: response.retMsg || 'Ëé∑ÂèñË¥∑Ê¨æÂàóË°®Â§±Ë¥•',
						icon: 'none'
					});
				}
			} catch (error) {
				console.error('Ëé∑ÂèñË¥∑Ê¨æÂàóË°®Â§±Ë¥•:', error);
				uni.showToast({
					title: 'Ëé∑ÂèñË¥∑Ê¨æÂàóË°®Â§±Ë¥•',
					icon: 'none'
				});
			} finally {
				if (isReset) {
					uni.hideLoading();
				}
				this.isLoading = false;
			}
		},

		// ÊêúÁ¥¢ÂÆ¢Êà∑
		searchCustomers() {
			// ÈáçÁΩÆÂπ∂ÊêúÁ¥¢
			this.loadPendingCustomers(true);
		},

		// Ëé∑ÂèñÁä∂ÊÄÅÊ†∑ÂºèÁ±ª
		getStatusClass(status) {
			if (!status) return 'pending';

			const statusMap = {
				'1': 'pending',
				'2': 'processing',
				'3': 'approved',
				'4': 'rejected'
			};

			return statusMap[status] || 'pending';
		},
		// ÂÆ°ÊâπÁä∂ÊÄÅÊñáÊú¨ËΩ¨Êç¢
		getDealStatus(status) {
			const statusMap = {
				1: 'ÂæÖÂ§ÑÁêÜ',
				2: 'ÂÆ°Êâπ‰∏≠',
				3: 'Â∑≤ÂÆ°Êâπ',
				4: 'Â∑≤ÊãíÁªù'
			}
			return statusMap[status] || 'Êú™Áü•'
		},	

		// ÂÆ¢Êà∑Á±ªÂûãËΩ¨Êç¢
		getClientType (type) {
			const typeMap = {
				1: 'Ê∂àË¥π',
				2: 'ÁªèËê•',
				3: 'Ê∂àË¥πÁªèËê•'
			}
			return typeMap[type] || 'Êú™Áü•'
		},

		// Êü•ÁúãÂÆ¢Êà∑ËØ¶ÊÉÖ
		handleViewCustomer(item) {
			const customerData = encodeURIComponent(JSON.stringify(item));
			uni.navigateTo({
				url: `/pages/customer/detail?id=${item.id}&customerData=${customerData}&type=loan`
			});
		},

		// ‰∏∫ÂÆ¢Êà∑ÂàõÂª∫Ë¥∑Ê¨æ
		handleCreateLoan(item) {
			// Ë∑≥ËΩ¨Âà∞Ë¥∑Ê¨æÁî≥ËØ∑È°µÈù¢ÔºåÂπ∂‰º†ÈÄíÂÆ¢Êà∑ID
			const customerData = encodeURIComponent(JSON.stringify(item));
			uni.navigateTo({
				url: `/pages/loan/apply?id=${item.id}&customerData=${customerData}`
			});
		},

		marriageChange(e) {
			this.customerForm.marriage = e.detail.value;
		},
		productChange(e) {
			this.loanForm.product = e.detail.value;
		},
		termChange(e) {
			this.loanForm.term = e.detail.value;
		},
		purposeChange(e) {
			this.loanForm.purpose = e.detail.value;
		},
		chooseImage(index) {
			uni.chooseImage({
				count: 1,
				sizeType: ['compressed'],
				sourceType: ['album', 'camera'],
				success: (res) => {
					this.uploadList[index].path = res.tempFilePaths[0];
				}
			});
		},
		resetForm() {
			this.customerForm = {
				name: '',
				phone: '',
				idCard: '',
				age: '',
				marriage: null
			};
			this.loanForm = {
				product: null,
				amount: '',
				term: null,
				purpose: null,
				monthlyIncome: ''
			};
			this.uploadList.forEach(item => {
				item.path = '';
			});
		},
		validateForm() {
			// Ë°®ÂçïÈ™åËØÅÈÄªËæë
			if (!this.customerForm.name) {
				uni.showToast({
					title: 'ËØ∑ËæìÂÖ•ÂÆ¢Êà∑ÂßìÂêç',
					icon: 'none'
				});
				return false;
			}
			if (!this.customerForm.phone) {
				uni.showToast({
					title: 'ËØ∑ËæìÂÖ•ÊâãÊú∫Âè∑Á†Å',
					icon: 'none'
				});
				return false;
			}
			if (!this.loanForm.amount) {
				uni.showToast({
					title: 'ËØ∑ËæìÂÖ•Ë¥∑Ê¨æÈáëÈ¢ù',
					icon: 'none'
				});
				return false;
			}
			if (this.loanForm.product === null) {
				uni.showToast({
					title: 'ËØ∑ÈÄâÊã©Ë¥∑Ê¨æ‰∫ßÂìÅ',
					icon: 'none'
				});
				return false;
			}
			return true;
		},
		submitLoan() {
			if (this.validateForm()) {
				uni.showLoading({
					title: 'Êèê‰∫§‰∏≠...'
				});

				// Ê®°ÊãüÊèê‰∫§
				setTimeout(() => {
					uni.hideLoading();
					uni.showToast({
						title: 'Êèê‰∫§ÊàêÂäü',
						icon: 'success'
					});

					// Êèê‰∫§ÊàêÂäüÂêéËøîÂõû
					setTimeout(() => {
						uni.switchTab({
							url: '/pages/dashboard/dashboard'
						});
					}, 1500);
				}, 2000);
			}
		}
	}
}
</script>

